import { fetchDetail,getLast7DayAlertTypeCount,postSearch,getIntraDayDnsAlarmDetail, getDnsAlarmDetail,getDnsAlarms,get24hDnsResponseCount,getIntraDayDnsAlarmDetailCount } from '@/api/alert/fetchDetail'
const moment = require('moment')

/**
 *  
 * @param {Object} resData 
 * @returns {Object} 
 *
 * chartsData:{
 *    charsX:[],
 *    "":[],
 *    "WEB":[],
 *  }
 *
 */
function getChartsData(resData) {

    // let alertType = ["","WEB","","WEB"]
    // let endTime = '2019-03-01T00:00:00.000+08:00'
    let endTime = new Date()
    let startEndList = []
    let chartsDataTyps = {}
    let chartsKeyList = []
    let i = 0
    while (i < 7) {
      let nowMoment = moment(endTime).subtract(i, 'days').format("YYYY-MM-DD")
      i++
      startEndList.push(nowMoment)
    }
    startEndList.reverse()
    // 
    Object.keys(resData).forEach((k)=>{
      let tmp = []
      chartsKeyList.push(k)
      startEndList.forEach((kv)=>{
        let tmpVal = ''
        Object.keys(resData[k]).forEach((y)=>{

          if(kv === moment(y).format("YYYY-MM-DD")){
            tmpVal = resData[k][y]
          }
        })
        tmp.push(tmpVal?tmpVal:0)
      })
      chartsDataTyps[k] = tmp
    })
    chartsDataTyps.keys = chartsKeyList

    startEndList.map((v,i)=>{
      startEndList[i] =  v.split('-')[2] + ''
    })
    chartsDataTyps.charsX = startEndList

    return chartsDataTyps
}


export default {
  namespaced: true,
  state: {
    alerts: [],
    detail:{},
    chartsSrcData:{},
    chartsDstData:{},
    searchMoreData:{},
    intraDayDnsAlarmDetail:[],
    dnsAlarmDetail:{},
    dnsAlarmsReqList:{},
    dnsAnswerList:[],
    dnsAlarmCount:{},
    DNSTitle:''
  },
  mutations: {
    UPDATE_ALERT: (state, alerts) => {
      state.alerts = alerts;
    },
    UPDATE_ALERT_Detail:(state, detail) => {
      state.detail = detail
    },
    UPDATE_CHARTS_SRC_DATA:(state, chartsData) => {
      state.chartsSrcData = chartsData
    },
    UPDATE_CHARTS_DST_DATA:(state, chartsData) => {
      state.chartsDstData = chartsData
    },
    UPDATE_SEARCH_MORE_DATA:(state, data) => {
      state.searchMoreData = data
    },
    UPDATE_DAY_DNS_ALERT_DETAIL_DATA:(state, data) => {
      state.intraDayDnsAlarmDetail = data
    },
    UPDATE_DNS_ALERT_DETAIL_DATA:(state, data) => {
      state.dnsAlarmDetail = data
    },
    UPDATE_DNS_ALERT_LIST_DATA:(state, data) => {
      state.dnsAlarmsReqList = data
    },
    UPDATE_DNS_ALERT_LIST_PAGE_DATA:(state, data) => {
      state.dnsAlarmsReqList.list = state.dnsAlarmsReqList.list.concat(data.list)
      state.dnsAlarmsReqList.pageNow = data.pageNow
      state.dnsAlarmsReqList.pageSize = data.pageSize
      state.dnsAlarmsReqList.totalCount = data.totalCount
      state.dnsAlarmsReqList.totalPageCount = data.totalPageCount
    },
    UPDATE_DNS_ANSWER_LIST_DATA:(state, data) => {
      state.dnsAnswerList = data
    },
    UPDATE_DNS_ALERT_COUNT_DATA:(state, data) => {
      state.dnsAlarmCount = data
    },
    UPDATE_DNS_TITLE_DATA:(state, data) => {
      state.DNSTitle = data
    }

  },
  actions: {
    async fetchAlerts({ commit }) {
      try {
        commit('UPDATE_ALERT', [] /**  */)
      } catch(e) {
        // do something
      }
    },
    async fetchAlarmDetail({commit},params) {

      // GET /app-alert/getAlarmDetail

      try {
        const ret = await fetchDetail(params)
        const resSrcDetail = await getLast7DayAlertTypeCount({ip:ret.data._source.alarm_src_ip})
        const resDstDetail = await getLast7DayAlertTypeCount({ip:ret.data._source.alarm_dst_ip})

        if(ret.success){
          commit('UPDATE_ALERT_Detail', ret.data /**  */)
          commit('UPDATE_CHARTS_SRC_DATA', getChartsData(resSrcDetail.data) /** ip */)
          commit('UPDATE_CHARTS_DST_DATA', getChartsData(resDstDetail.data) /** ip */)
        }
      } catch (e) {
        // do something
      }

    },
    async fetchSearch({commit},params) {
      try {
        const ret = await postSearch(params)
        if(ret.success){
          commit('UPDATE_SEARCH_MORE_DATA', ret.data /** ip */)

        }
      } catch(e) {
        // do something
      }
    },
    async fetchIntraDayDnsAlarmDetail({commit},params) {

      try {
        const ret = await getIntraDayDnsAlarmDetail(params)
        if(ret.success && ret.data){
          let dataArray = moment(ret.data[0]._source.alarm_find_time).toArray()
          let domain = ret.data[0]._source.dns_questionname_domain
          let DNSTitle = `${dataArray[1]}/${dataArray[2]}  ${domain} DNS`

          commit('UPDATE_DAY_DNS_ALERT_DETAIL_DATA', ret.data /** Dns - */)
          commit('UPDATE_DNS_TITLE_DATA', DNSTitle /** Dns - -title*/)

        }
      } catch (e) {
        // do something
      }
    },
    async fetchDnsAlarmDetail({commit},params) {
      try {
        const ret = await getDnsAlarmDetail(params)
        const ret3 = await get24hDnsResponseCount(params)
        const ret4 = await getIntraDayDnsAlarmDetailCount(params)

        if(ret.success){
          commit('UPDATE_DNS_ALERT_DETAIL_DATA', ret.data /** Dns */)
          commit('UPDATE_DNS_ANSWER_LIST_DATA', ret3.data  /** Dns */)
          commit('UPDATE_DNS_ALERT_COUNT_DATA', ret4.data  /** Dns */)
        }
      } catch (e) {
        // do something
      }
    },
    async fetchDnsAlarmsPageIndex({commit},params) {

      try {
        const ret2 = await getDnsAlarms(params)
        if(ret2.success){
          if(params.pageNum !== 0){
            commit('UPDATE_DNS_ALERT_LIST_PAGE_DATA', ret2.data  /** Dns */)
          }else{
            commit('UPDATE_DNS_ALERT_LIST_DATA', ret2.data  /** Dns */)
          }
        }
      } catch (e) {
        // do something
      }

    }}
}
