import {
    fetchTypes,
    fetchRank,
    fetchLast24hTypeCount,
    fetchLast24hRankCount,
    fetchLast7DayRankCount
} from '@/api/alert/fetchType'
// const moment = require('moment')


export default {
    namespaced: true,
    state: {
        alertTypes: [],
        alertLevels: [],
        // 24 
        last24hAlertRankCounts: {},
        last24hAlertTypeCounts: [],
        last7DayAlertRankCounts: {}

    },
    /**
     *  getters 
     * 
     */
    getters: {
        alertLevels: (state) => state.alertLevels,
        alertTypes: (state) => state.alertTypes
    },
    mutations: {
        UPDATE_ALERT_TYPE: (state, alertTypes) => {
            state.alertTypes = alertTypes;
        },
        UPDATE_ALERT_LEVEL: (state, alertLevels) => {
            state.alertLevels = alertLevels;
        },
        UPDATE_LAST_24H_AlERT_TYPE_COUNT: (state, last24hAlertTypeCounts) => {
            state.last24hAlertTypeCounts = last24hAlertTypeCounts;
        },
        UPDATE_LAST_24H_AlERT_RANK_COUNT: (state, last24hAlertRankCounts) => {
            state.last24hAlertRankCounts = last24hAlertRankCounts;
        },
        UPDATE_LAST_7DAY_AlERT_RANK_COUNT: (state, last7DayAlertRankCounts) => {
            state.last7DayAlertRankCounts = last7DayAlertRankCounts;

        }
    },
    actions: {
        async fetchAlertTypes({
            commit,
            state
        }) {
            try {
                if (state.alertTypes.length <= 0) {
                    const ret = await fetchTypes();
                    const types = [];
                    for (let key in ret.data) {
                        types.push({
                            key: key,
                            value: ret.data[key]
                        })
                    }
                    commit('UPDATE_ALERT_TYPE', types)
                }
            } catch (e) {
                console.error(e);
            }
        },
        async fetchAlertLevels({
            commit,
            state
        }) {
            /*
            const ret = await ajax.getAlertLevels();
            */
            try {
                if (state.alertLevels.length <= 0) {
                    const ret = await fetchRank();
                    const levels = [];
                    for (let key in ret.data) {
                        levels.push({
                            key: key,
                            value: ret.data[key]
                        })
                    }
                    commit('UPDATE_ALERT_LEVEL', levels)
                }
            } catch (e) {
                console.error(e);
            }
        },
        // 24
        async fetchLast24hAlertTypeCounts({
            commit,
            state
        }) {
            try {
                if (state.last24hAlertTypeCounts.length <= 0) {
                    const ret = await fetchLast24hTypeCount();
                    const types = [];
                    for (let key in ret.data) {
                        types.push({
                            key: key,
                            value: ret.data[key]
                        })
                    }
                    commit('UPDATE_LAST_24H_AlERT_TYPE_COUNT', types)
                }
            } catch (e) {
                console.error(e);
            }
        },
        // 24
        async fetchLast24hAlertRankCounts({
            commit
        }) {
            try {
                const ret = await fetchLast24hRankCount();
                commit('UPDATE_LAST_24H_AlERT_RANK_COUNT', ret.data)
            } catch (e) {
                console.error(e);
            }
        },
        // last7DayAlertRankCounts  7
        async fetchLast7DayAlertRankCounts({
            commit
        }) {
            try {
                // if (state.last7DayAlertRankCounts.length <= 0) {
                //   const ret = await fetchLast7DayRankCount();
                //   const types = [];
                //   for (let key in ret.data) {
                //     types.push({
                //       key: key,
                //       value: ret.data[key]
                //     })
                //   }

                // }
                const ret = await fetchLast7DayRankCount();
                commit('UPDATE_LAST_7DAY_AlERT_RANK_COUNT', ret.data)
            } catch (e) {
                console.error(e)
            }
        },

    }
}