import { fetchSearch ,searchRecord} from '@/api/alert/dashboard/list'
import * as moment from 'moment'
import { deepClone, cleanArray } from '@/utils/index.js'

export default {
  namespaced: true,
  state: {
    alerts: [],
    alertList: [],
    pageInfo: null,
    recent:[],
    commonUse:[]
  },
  getters: {
    alertList: (state) => state.alertList,
    pageInfo: (state) => state.pageInfo,
    recent: (state) => {
      let ret = deepClone(state.recent);
      ret.map(el => {
        let iTimes='',
        { alarmRank, alarmType, beginTime, endTime, ip, port, alarmDetail } = {...el._source};
        el.tagList = [];
        el.params = {alarmRank, alarmType,beginTime ,endTime, ip, port, alarmDetail};
        for( let key in el.params){
          if(el.params[key] === undefined){
            delete el.params[key];
          }else if(key !== 'beginTime'&& key !== 'endTime' ){
            el.tagList.push({
              key:key,
              value: el.params[key]
            })
          }
        }
        //  
        if(beginTime && endTime) {
          iTimes = moment(beginTime).format('YYYY-MM-DD HH:mm:ss') +'~'+moment(endTime).format('YYYY-MM-DD HH:mm:ss');
          el.tagList.push({
            key: 'iTimes',
            value: iTimes
          })
        }
        el.show =  '"'+ cleanArray([alarmRank, alarmType,iTimes, ip, port, alarmDetail]).join('" "') + '"';
      });
      return ret;
    },
    commonUse: (state) => {
      let ret = deepClone(state.commonUse);
      ret.map(el => {
        let iTimes='',
        { alarmRank, alarmType, beginTime, endTime, ip, port, alarmDetail } = {...el._source};
        el.tagList = [];
        el.params = {alarmRank, alarmType,beginTime ,endTime, ip, port, alarmDetail};
        for( let key in el.params){
          if(el.params[key] === undefined){
            delete el.params[key];
          }else if(key !== 'beginTime'&& key !== 'endTime' ){
            el.tagList.push({
              key:key,
              value: el.params[key]
            })
          }
        }
        //  
        if(beginTime && endTime) {
          iTimes = moment(beginTime).format('YYYY-MM-DD HH:mm:ss') +'~'+moment(endTime).format('YYYY-MM-DD HH:mm:ss');
          el.tagList.push({
            key: 'iTimes',
            value: iTimes
          })
        }
        el.show =  '"'+ cleanArray([alarmRank, alarmType,iTimes, ip, port, alarmDetail]).join('" "') + '"';
      });
      return ret;
    }
  },
  mutations: {
    UPDATE_ALERT: (state, alerts) => {
      state.alerts = alerts;
    },
    UPDATE_ALERT_LIST: (state, alertList) => {
      state.alertList = alertList;
    },
    UPDATE_ALERT_PAGE: (state, pageInfo) => {
      state.pageInfo = pageInfo;
    },
    UPDATE_ALERT_COMMON_RECENT: (state, recent) => {
      state.recent = recent;
    },
    UPDATE_ALERT_COMMON_RECORD: (state, commonUse) => {
      state.commonUse = commonUse;
    }
  },
  actions: {
    async fetchAlerts({ commit }, query) {
      try {
        const ret = await fetchSearch(query);
        let list = [], page={};
        for (let key in ret.data) {
          if( key === 'list') {
            list = ret.data[key];
          }else {
            page[key] = ret.data[key];
          }
        }
        commit('UPDATE_ALERT_LIST', list);
        commit('UPDATE_ALERT_PAGE', page);
      } catch(e) {
        console.error(e);
      }
    },
    async searchRecordAlarm({ commit }) {
      try {
        const ret = await searchRecord();
        commit('UPDATE_ALERT_COMMON_RECORD', ret.data.commonUse);
        commit('UPDATE_ALERT_COMMON_RECENT', ret.data.recent);
      } catch(e) {
        console.error(e);
      }
    }

  }
}
