import axios from 'axios';
import { Message, Notification, MessageBox } from 'element-ui';

/*
 * url  api
 * params 
 * opts {
 *   headers: 
 *   success: 
 *   failed: 
 *   tips: 0,1,2,3 // 0 1 notice2confirm3
 * }
 */

class Http {
    constructor(opts = {}) {
        const {
            timeout = 30000,
                // baseUrl = '/app-api',
                baseUrl = '',
                contentType = 'application/json',
                withCredentials = true
        } = opts;

        this.timeout = timeout;
        this.baseUrl = baseUrl;
        this.contentType = contentType;
        this.withCredentials = withCredentials;
    }

    get(url, params = {}, opts = {}) {
        return this.ajax('get', url, {
            params
        }, opts);
    }

    del(url, params = {}, opts = {}) {
        return this.ajax('delete', url, {
            params
        }, opts);
    }

    put(url, data = {}, opts = {}) {
        return this.ajax('put', url, {
            data
        }, opts);
    }

    post(url, data = {}, opts = {}) {
        return this.ajax('post', url, {
            data
        }, opts);
    }

    ajax(method, url, params, opts) {
        const ajaxRequest = axios({
            method,
            url: opts.baseUrl ? opts.baseUrl + url : this.baseUrl + url,
            headers: {
                'Content-Type': this.contentType,
                ...opts.headers
            },
            timeout: opts.timeout ? opts.timeout : this.timeout,
            onUploadProgress: opts.uploading,
            onDownloadProgress: opts.download,
            withCredentials: opts.withCredentials !== undefined ? opts.withCredentials : this.withCredentials,
            ...params
        });

        return ajaxRequest.then((res) => {
            const {
                data = null,
                    status = 200,
                    statusText = ''
            } = res || {};
            let ret = null;
            if (status === 200) {
                if (opts.success) {
                    opts.success(data.data);
                }
                ret = data.data;
            } else {
                ret = Http.errorHandler({
                    ...data,
                    statusCode: status,
                    statusText: statusText
                }, opts);
            }
            return ret;
        }).catch((err) => {
            const {
                data = {
                    statusCode: 10500,
                    statusText: '',
                    data: null
                }
            } = err.response || {};

            return Http.errorHandler(data, opts);
        });
    }

    static errorHandler(data, opts) {
        if (opts.failed && typeof opts.failed === 'function') {
            opts.failed(data);
        }
        // 0,1notice2alert3
        switch (opts.tips) {
            case 1:
                Message({
                    message: data.statusText,
                    center: true,
                    type: 'error'
                });
                break;
            case 2:
                MessageBox.alert(data.statusText, '', {
                    type: 'error',
                    confirmButtonText: ''
                });
                break;
            case 3:
                Notification({
                    type: 'error',
                    title: '',
                    message: data.statusText
                });
                break;
            default:
                break;
        }

        return data.data;
    }
}

const http = new Http();

export default http;